import './../../../services/modal/modal-service';
import NotificationsDialogController from './notifications-dialog/notifications-dialog-controller';
import SaveAsFavoriteDialogController from './save-as-favorite/save-as-favorite-dialog-controller';

ShipmentCompleteController.$inject = ['modalService'];

export default function ShipmentCompleteController(modalService) {
    const vm = this;

    Object.assign(vm, {
        showNotificationsDialog,
        showSaveAsFavoriteDialog
    });

    function showNotificationsDialog() {
        modalService.showDialog({
            controller: NotificationsDialogController,
            controllerAs: 'notificationsDialogCtrl',
            template:'<div class=shipment-notification ewf-modal dialog-width=full nls-title=shipment.shipment_complete_notifications_dialog_title><form class=\"ewf-modal__body shipment-notification__form\" name=notificationsForm novalidate ewf-form=notificationsDialogEwfForm ng-class=\"{ \'is-submitted\': notificationsForm.$submitted }\" ng-submit=notificationsDialogCtrl.submitNotifications();><div class=\"row shipment-notification__header\"><div class=col-2><label class=label nls=shipment-notifications.pickup></label></div><div class=col-2><label class=label nls=shipment-notifications.clearance_delay></label></div><div class=col-2><label class=label nls=shipment-notifications.customs_clearance></label></div><div class=col-2><label class=label nls=shipment-notifications.exception></label></div><div class=col-2><label class=label nls=shipment-notifications.out_for_delivery></label></div><div class=col-2><label class=label nls=shipment-notifications.delivered></label></div></div><div class=row ng-repeat=\"notification in notificationsDialogCtrl.notifications\" ng-form=notificationForm><div class=row><div class=col-5><div class=col-3><div class=field-wrapper><label class=label nls=shipment.shipment_complete_notifications_dialog_type_label></label><div class=\"select select_width_full\"><select required ng-change=notificationsDialogCtrl.onNotificationTypeChange(notification) ng-model=notification.type><option value=email nls=shipment.shipment_complete_notifications_dialog_email_option_label></option><option value=sms nls=shipment.shipment_complete_notifications_dialog_sms_option_label></option></select></div></div></div><div class=col-9><div ng-if=\"notification.type === \'sms\'\"><div class=\"col-4 field form-row\"><label class=label nls=shipment.shipment_complete_notifications_dialog_phone_code_label></label><div class=phone-autocomplete><i class=\"phone-autocomplete__flag flag flag_small\"></i> <input class=\"input input_width_full\" name=phoneCode type=text placeholder=+___ required ng-model=notification.phoneCode ui-mask=+9?9?9 ui-options=\"{clearOnBlur : false}\"><div class=msg-error ng-if=notificationsDialogCtrl.getFieldError(notificationForm.phoneCode) ng-switch=notificationsDialogCtrl.getFieldError(notificationForm.phoneCode)><div ng-switch-when=required nls=errors.required_error_message></div></div></div></div><div class=\"col-8 field form-row\"><label class=label nls=shipment.shipment_complete_notifications_dialog_phone_number_label></label> <input class=\"input input_width_full\" name=phone type=text placeholder=\"(___) ___-____\" required ng-model=notification.phone ui-mask=\"(999) 999-9999\" ui-options=\"{clearOnBlur : false}\"><div class=msg-error ng-if=notificationsDialogCtrl.getFieldError(notificationForm.phone) ng-switch=notificationsDialogCtrl.getFieldError(notificationForm.phone)><div ng-switch-when=required nls=errors.required_error_message></div><div ng-switch-when=mask nls=errors.pattern_error_message></div></div></div></div><div class=\"field form-row\" ewf-field=email ng-if=\"notification.type === \'email\'\"><label class=label nls=shipment.shipment_complete_notifications_dialog_email_label></label> <input class=\"input input_width_full\" name=email type=email placeholder=jsmith@email.com ng-model=notification.email ewf-input=notificationsDialogEwfForm.email ewf-validate-required ewf-validate-pattern=EMAIL><div ewf-field-errors></div></div></div></div><div class=col-5 ng-form=notificationSettingsForm><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-pickup_{{$index}} ng-model=notification.settings.pickup> <span class=label></span></label></div><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-clearance-delay_{{$index}} ng-model=notification.settings.clearanceDelay> <span class=label></span></label></div><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-customs-clearance_{{$index}} ng-model=notification.settings.customsClearance> <span class=label></span></label></div><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-exception_{{$index}} ng-model=notification.settings.exception> <span class=label></span></label></div><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-out-for-delivery_{{$index}} ng-model=notification.settings.outForDelivery> <span class=label></span></label></div><div class=shipment-notification__checkbox><label class=checkbox><input class=checkbox__input type=checkbox data-aqa-id=notification_check-delivered_{{$index}} ng-model=notification.settings.delivered> <span class=label></span></label></div></div><div class=\"col-2 shipment-notification__remove\"><button class=\"btn btn_action\" type=button ng-if=\"notificationsDialogCtrl.notifications.length > 1\" ng-click=notificationsDialogCtrl.deleteNotification($index);><i class=dhlicon-cancel></i> <span nls=shipment.shipment_complete_notifications_dialog_remove_button_label></span></button></div></div><div class=row><div class=\"col-5 offset-5 shipment-notification__settings-wrap\"><input name=settings type=hidden ng-model=notification.settings ewf-non-empty-validator><div class=\"msg-error shipment-notification__msg-error\" ng-if=\"(notificationsForm.$submitted || notificationSettingsForm.$dirty) && notificationForm.settings.$invalid\" nls=shipment.shipment_complete_notifications_dialog_settings_error></div></div></div></div><button class=\"btn btn_animate btn_small\" type=button ng-click=notificationsDialogCtrl.addNotification();><i class=dhlicon-add></i> <span class=btn__text nls=shipment.shipment_complete_notifications_dialog_add_button_label></span></button><div class=msg-error ng-if=notificationsDialogCtrl.error nls={{notificationsDialogCtrl.error}}></div><div class=ewf-modal__footer><button class=\"btn btn_success right\" type=submit nls=shipment.shipment_complete_notifications_dialog_notify_button_label></button></div></form></div>'
        });
    }

    function showSaveAsFavoriteDialog() {
        modalService.showDialog({
            controller: SaveAsFavoriteDialogController,
            controllerAs: 'saveAsFavoriteDialogCtrl',
            template:'<div ewf-modal nls-title=shipment.shipment_complete_save_as_favorite_dialog_title><form name=saveAsFavoriteForm ewf-form=saveAsFavoriteEwfFormCtrl ng-submit=\"saveAsFavoriteDialogCtrl.saveAsFavorite(saveAsFavoriteForm, ewfFormCtrl)\"><div class=section__content><div class=form-line><label nls=shipment.shipment_complete_save_as_favorite_dialog_save_as_label></label></div><div class=\"form-line field\" ewf-field=shipmentName><input type=text name=shipmentName class=\"input input_width_half\" autocomplete=off nls=shipment.shipment_complete_save_as_favorite_dialog_input ng-model=saveAsFavoriteDialogCtrl.shipmentName ewf-input=saveAsFavoriteEwfFormCtrl.shipmentName ewf-validate-required><div ewf-field-errors></div></div><div class=form-line><button class=\"btn btn_success btn_regular\" type=submit nls=shipment.shipment_complete_save_as_favorite_dialog_save_and_continue_button_label></button></div><div class=msg-error ng-repeat=\"error in saveAsFavoriteDialogCtrl.errors\" nls={{error}}></div></div></form></div>'
        });
    }
}
